@import url("https://fonts.googleapis.com/css2?family=Exo+2:wght@100&display=swap");

* {
  margin: 0%;
  padding: 0;
  box-sizing: border-box;
}

@mixin pseudo-elements-gradient($rotation) {
  content: "";
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    $rotation,
    rgba(2, 0, 36, 0.8) 0%,
    rgba(0, 0, 0, 0.8) 0%,
    rgba(0, 212, 255, 0) 45%
  );
}

.App {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: url(assets/image/bg.jpg);
  &:after {
    @include pseudo-elements-gradient(180deg);
  }
  &:before {
    @include pseudo-elements-gradient(0deg);
  }
  &-title {
    color: white;
    font-size: 36pt;
    margin: 15px 0;
    font-family: "Exo 2";
    font-weight: lighter;
    text-align: center;
    position: relative;
    z-index: 2;
  }
  &-frame-cards {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 10px 65px !important;
  }
}

@media screen and (max-width: 1280px) {
  .App-frame-cards {
    justify-content: center !important;
  }
}
@media screen and (max-width: 1280px) {
  .App {
    &-title {
      font-size: 30pt !important;
    }
  }
}
@media screen and (max-width: 700px) {
  .App {
    &-title {
      font-size: 24pt !important;
    }
  }
}

@media screen and (max-width: 450px) {
  .App {
    &-title {
      font-size: 18pt !important;
    }
  }
}